.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 63%;
  padding: 5rem 0;
  height: auto;
  overflow: hidden;
}
.header__logo {
  width: 20rem;
  height: auto;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: unset;
  }
}
.header__descripcion {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 50rem;
}
.header__heading {
  color: #FFFFFF;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .header__heading {
    width: 50rem;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 6rem;
  }
}
.header__boton {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.header__boton:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .header__boton {
    width: auto;
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .header__boton {
    width: fit-content;
  }
}

.crear {
  display: flex;
  flex-direction: column;
  padding: 8rem 1rem 5rem;
  gap: 3rem;
}
@media (min-width: 768px) {
  .crear {
    flex-direction: row;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .crear__imagen {
    width: 50%;
  }
}
.crear__heading {
  font-size: 3.6rem;
  color: #313c4e;
  box-shadow: inset 0 -11px 0 0 #84d9bc;
  width: fit-content;
}

.ganancias {
  background-color: #84d9bc;
  padding: 8rem 1rem;
}
.ganancias__contenido {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .ganancias__contenido {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.ganancias__heading {
  font-size: 3.6rem;
  color: #313c4e;
  box-shadow: inset 0 -11px 0 0 #FFFFFF;
  width: fit-content;
}
@media (min-width: 768px) {
  .ganancias__texto {
    grid-column: 1/4;
  }
}
@media (min-width: 768px) {
  .ganancias__imagen-contenedor {
    grid-column: 4/6;
  }
}
.ganancias__imagen {
  width: 100%;
  height: auto;
  transition: transform 0.3s;
}
.ganancias__imagen:hover {
  transform: rotate(6deg) scale(1.1);
}

.estadisticas {
  padding: 8rem 1rem;
}
.estadisticas__heading {
  font-size: 3.6rem;
  color: #313c4e;
  box-shadow: inset 0 -11px 0 0 #84d9bc;
  width: fit-content;
  margin-bottom: 4rem;
}

.planes {
  background-color: #313c4e;
}
.planes__contenido {
  padding: 8rem 1rem;
}
.planes__heading {
  font-size: 3.6rem;
  color: #FFFFFF;
  box-shadow: inset 0 -11px 0 0 #84d9bc;
  width: fit-content;
  margin-bottom: 3.5rem;
}
.planes__texto {
  color: #FFFFFF;
  margin-bottom: 3rem;
}

.membresias {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .membresias {
    width: 50%;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .membresias {
    margin: 12rem auto 5rem;
    width: 100rem;
    display: grid;
    gap: 0;
    grid-template-columns: repeat(7, 1fr);
    position: relative;
  }
}
.membresias__tipo {
  padding-top: 2rem;
  background-color: #FFFFFF;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 12rem 2fr;
  background-repeat: no-repeat;
  background-size: 10rem;
  background-position: center 7%;
  border-bottom: 1px dashed #313c4e;
}
@media (min-width: 1024px) {
  .membresias__tipo {
    transition: transform 0.3s;
  }
  .membresias__tipo:hover {
    transform: scale(1.1);
  }
}
.membresias__tipo--inicio {
  background-image: url(../img/icono_inicio.svg);
  grid-row: 3/4;
  border: none;
}
@media (min-width: 1024px) {
  .membresias__tipo--inicio {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.membresias__tipo--pro {
  background-image: url(../img/icono_pro.svg);
  grid-row: 2/3;
}
@media (min-width: 1024px) {
  .membresias__tipo--pro {
    grid-column: 6/7;
    grid-row: 1/2;
    border: none;
  }
}
.membresias__tipo--negocio {
  background-color: #84d9bc;
  background-image: url(../img/icono_negocio.svg);
  grid-row: 1/2;
}
@media (min-width: 1024px) {
  .membresias__tipo--negocio {
    grid-column: 3/6;
    grid-row: 1/2;
    border: none;
    position: absolute;
    z-index: 3;
    width: 100%;
    border-bottom-right-radius: 3rem;
    border-bottom-left-radius: 3rem;
  }
}
@media (min-width: 1024px) {
  .membresias__tipo--negocio::before {
    content: "";
    display: block;
    background-color: #84d9bc;
    height: 6rem;
    top: -68px;
    position: relative;
    border-top-right-radius: 3rem;
    border-top-left-radius: 3rem;
  }
}
.membresias__info {
  grid-row: 2/3;
  padding: 2rem 3rem 3rem;
  text-align: center;
}
.membresias__heading {
  margin-bottom: 3rem;
}
.membresias__precio {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1;
  margin: 3rem 0;
}
.membresias__beneficios {
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.membresias__boton {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.membresias__boton:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .membresias__boton {
    width: auto;
    display: inline-block;
  }
}
.membresias__boton--negocio {
  background-color: transparent;
  border: 3px solid #FFFFFF;
}

.testimoniales {
  padding: 8rem 1rem;
}
@media (min-width: 768px) {
  .testimoniales {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 3rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .testimoniales {
    gap: 5rem;
  }
}
@media (min-width: 768px) {
  .testimoniales__contenido {
    grid-column: 2/4;
  }
}
.testimoniales__heading {
  font-size: 3.6rem;
  color: #313c4e;
  box-shadow: inset 0 -11px 0 0 #84d9bc;
  width: fit-content;
  margin-bottom: 4rem;
}
.testimoniales__card {
  background-color: #84d9bc;
  padding: 2rem 2rem 2rem 10rem;
  margin-bottom: 2rem;
  border: 5px solid #313c4e;
  border-radius: 2rem;
  position: relative;
}
.testimoniales__card::before {
  content: "";
  display: block;
  width: 9rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  position: absolute;
  top: 20px;
  left: 20px;
}
@media (min-width: 1024px) {
  .testimoniales__card {
    width: 80%;
  }
  .testimoniales__card:nth-child(2) {
    margin-left: 6rem;
  }
  .testimoniales__card:nth-child(3) {
    margin-left: 12rem;
  }
}
.testimoniales__texto {
  margin: 0;
  font-size: 1.6rem;
}
@media (min-width: 1024px) {
  .testimoniales__texto {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .testimoniales__imagen-contenedor {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
.testimoniales__imagen {
  width: 80%;
  height: auto;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimoniales__imagen {
    width: 100%;
  }
}

.footer {
  background-color: #313c4e;
  padding: 5rem 0 0;
}
.footer__grid {
  padding-bottom: 3.5rem;
  border-bottom: 1px solid #84d9bc;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__imagen {
  height: auto;
  margin: 0 auto;
  width: 20rem;
  grid-row: 4/5;
}
@media (min-width: 768px) {
  .footer__imagen {
    grid-row: unset;
  }
}
.footer__heading {
  text-align: center;
  margin: 0 auto 2rem;
  font-size: 2.8rem;
  text-transform: uppercase;
  color: #84d9bc;
  box-shadow: inset 0 -1px 0 0 #FFFFFF;
  width: fit-content;
}
@media (min-width: 768px) {
  .footer__heading {
    font-size: 2.4rem;
  }
}
@media (min-width: 1024px) {
  .footer__heading {
    font-size: 2.8rem;
  }
}
.footer__link {
  display: block;
  color: #FFFFFF;
  text-align: center;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer__copyright {
  margin: 0;
  padding: 3.5rem 0;
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 1.4rem;
  text-align: center;
  line-height: 1.3;
}