@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    @include m.bgImg('../img/header.jpg', v.$secundarioOpacity);
    background-position: 63%;
    padding: 5rem 0;
    height: auto;
    overflow: hidden;
    &__logo {
        width: 20rem;
        height: auto;
        margin: 0 auto;
        @include m.tablet {
            margin: unset;
        }
    }
    &__descripcion {
        margin-top: 5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 50rem;
    }
    &__heading {
        color: v.$blanco;
        font-size: 4rem;
        @include m.tablet {
            width: 50rem;
        }
        @include m.desktop {
            font-size: 6rem;
        }
    }
    &__boton {
        @include m.boton(v.$primario, v.$secundario);
        @include m.tablet {
            width: fit-content;
        }
    }
}