@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$secundario;
    padding: 5rem 0 0;
    &__grid {
        padding-bottom: 3.5rem;
        border-bottom: 1px solid v.$primario;
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(4, 2rem);
        }
    }
    &__imagen {
        height: auto;
        margin: 0 auto;
        width: 20rem;
        grid-row: 4 / 5;
        @include m.tablet {
            grid-row: unset;
        }
    }
    &__heading {
        text-align: center;
        margin: 0 auto 2rem;
        font-size: 2.8rem;
        text-transform: uppercase;
        color: v.$primario;
        box-shadow: inset 0 -1px 0 0 v.$blanco;
        width: fit-content;
        @include m.tablet {
            font-size: 2.4rem;
        }
        @include m.desktop {
            font-size: 2.8rem;
        }
    }
    &__link {
        display: block;
        color: v.$blanco;
        text-align: center;
    }
    &__nav {
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    &__copyright {
        margin: 0;
        padding: 3.5rem 0;
        color: v.$blanco;
        text-transform: uppercase;
        font-size: 1.4rem;
        text-align: center;
        line-height: 1.3;
    }
}